<template>
  <div class="kaoqin">
    <div class="nav-bar-white">
      <headerBar :header-data="headerData" />
    </div>
    <div class="kaoqin-head">
      <div class="kaoqin-userinfo-bg" />
      <div class="kaoqin-userinfo">
        <div class="userinfo-tx" @click="showImage(userInfo.tx_url?userInfo.tx_url:avatar)">
          <van-image
            :src="
              userInfo.tx_url?userInfo.tx_url:avatar"
            fit="cover"
            :round="true"
          />
        </div>
        <div class="userinfo-name">
          <div class="name">{{ userInfo.name }}</div>
          <div class="info-flex">
            <div class="info">
              <i class="iconfont icondailijigou" />
              {{ userInfo.bmmc }}
            </div>
            <div class="info">
              <i class="iconfont iconshouji" />
              {{ userInfo.sj }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="kaoqin-time"><i class="iconfont iconalarm_clock" /><em>当前时间：</em>{{ date }}</div>
    <div class="content">
      <div class="kaoqin-item">
        <div class="kaoqin-list">
          <div class="kaoqin-icon am" />
          <div class="kaoqin-type">
            <div v-for="(item,index) of amUpdateQdData" :key="index">
              <div v-if="item.worktype" class="type" @click="showPopupKqlx(item)">{{ QdShareDataList[item.worktype] }}  {{ item.worktype!=='zc'?item.address:'' }}</div>
            </div>
          </div>
          <div v-if="amUpdateQdData[0].id" class="kaoqin-state done">已提交</div>
          <div v-else class="kaoqin-state ">未提交</div>
        </div>

        <div class="kaoqin-list">
          <div class="kaoqin-icon pm" />
          <div class="kaoqin-type">
            <div v-for="(items,indexs) of pmUpdateQdData" :key="indexs">
              <div v-if="items.worktype" class="type" @click="showPopupKqlx(items)">
                {{ QdShareDataList[items.worktype] }} {{ items.worktype!=='zc'?items.address:'' }}</div>
            </div>
          </div>
          <div v-if="pmUpdateQdData[0].id" class="kaoqin-state done">已提交</div>
          <div v-else class="kaoqin-state ">未提交</div>
        </div>

      </div>
      <van-notice-bar v-show="showTip" left-icon="info-o" class="notice-bar-white" scrollable>
        受移动设备网络以及当前位置海拔等因素影响，导致定位不能正常进行位置更新.请适当调整当前位置并多次尝试.
      </van-notice-bar>

      <div class="login-button">
        <!-- <van-button round type="info" @click="goKqtxl()">上传签到</van-button> -->
        <van-button round type="info" :disabled="isdisabled" @click="sendQdSj()">上传签到</van-button>
      </div>

    </div>

    <!-- 考勤类型弹窗 begin -->
    <van-popup v-model="KqlxShow" round closeable position="bottom" :style="{ height: '85%' }">
      <div class="common-popup">
        <div class="popup-title">选择考勤类型</div>
        <div class="popup-content">
          <div class="select-list select-list-line">
            <ul v-for="(item,index) of QdAmData" :key="index" @click="cheackQdType(index)">
              <li :class="checkIndex===index?'active':''">{{ item.title }}</li>
            <!-- <li class="active">市内办事</li>
              -->
            </ul>
          </div>
          <van-field
            v-if="showCsdd"
            v-model="csdd"
            label="出差地点"
            required
            placeholder="输入出差地点"
            :rules="[{ required: true, message: '请输入出差地点' }]"
          />
          <div v-if="showCsdd">请输入出差地点，到市一级，例如：广东佛山市</div>
          <div v-if="showJjbg">{{ locationMsg.address1 }}</div>
          <van-field
            v-if="showSnbs"
            v-model="bsdd"
            label="办事地点"
            required
            placeholder="输入办事地点"
            :rules="[{ required: true, message: '请输入办事地点' }]"
          />
        </div>
        <!-- 底部按钮 begin -->
        <div v-if="showSnbs" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="addSnType(0)">清空</van-button></li>
            <li><van-button type="info" round size="large" @click="addSnType(1)">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
        <!-- 底部按钮 begin -->
        <div v-if="showCsdd" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="addCcType(0)">清空</van-button></li>
            <li><van-button type="info" round size="large" @click="addCcType(1)">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
        <!-- 底部按钮 begin -->
        <div v-if="showJjbg" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="qr(0)">重新获取</van-button></li>
            <li><van-button type="info" round size="large" @click="qr(1)">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
      </div>
    </van-popup>
    <!-- 考勤类型弹窗 end -->
    <div class="common-space" />
    <FooterNav ref="FooterNav" />
  </div>
</template>

<script land="script" scoped>
import { getUserName } from '@/common/js/catch'
import FooterNav from '@/components/footerNav/footerNav.vue'
import headerBar from '@/components/header/headerBar.vue'
import constants from '@/common/js/constants.js'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
import Loading from '@/components/spin/index.js'
import { ImagePreview } from 'vant'
import { JSEncrypt } from 'jsencrypt'
import { Base64 } from 'js-base64'

const loading = Loading.get({})
export default {

  components: {
    headerBar,
    FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      btnState: true,
      checkIndex: '',
      date: '',
      headerData: {
        title: '考勤打卡'
      },
      avatar: require('@/assets/images/default-tx.png'), // 头像地址
      QdAmData: '',
      showTip: false, // 处理定位信息提示
      showSpinner: false, // 展示加载框
      showCsdd: false, // 是否展示出差地点
      showSnbs: false, // 是否展示市内办事地点
      showJjbg: false, // 是否展示居家办公
      csdd: '', // 出差地点
      bsdd: '', // 办事地点
      checkPopupList: -1, // 弹出列表选择项
      showPopup: '', // 0 2--上午，1 3--下午
      KqlxShow: false,
      isdisabled: false, // 按钮是否可以点击
      gaodeDW: { // 高德储存地图
        lng: '',
        lat: ''
      },
      // 0是考勤机，1是手机APP
      amUpdateQdData: [{ id: '', dayType: '上午', times: 0.5, worktype: 'wzt', source: '1', address: ''
      }], // 上午签到数据数据类型
      pmUpdateQdData: [{ id: '', dayType: '下午', times: 0.5, worktype: 'wzt', source: '1', address: ''
      }], // 下午签到数据类型
      locationMsg: {}, // 地址信息
      getAddressType: '' // 获取地址类型  '':打卡获取  'jjbg'：居家办公获取
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  mounted() {
    const _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      _this.date = _this.getNowTime() // 修改数据date
    }, 1000)
    var self = this
    window['updateLocation1'] = function(value) {
      console.log('value======123==============', value)
      if (value.lon === '0' || value.lat === '0') {
        loading.hideLoading()
        self.$toast('定位数据获取失败！')
        self.btnState = true
      } else {
        loading.hideLoading()
        if (self.amUpdateQdData[0].worktype === 'zc') {
          self.amUpdateQdData[0].address = value.address
        }
        if (self.pmUpdateQdData[0].worktype === 'zc') {
          self.pmUpdateQdData[0].address = value.address
        }
        self.goKqtxl()
      }
    }
    window['updateLocation'] = function(value) {
      if (self.getAddressType === 'jjbg') {
        console.info('临时坐标-----', value.lon, value.lat, value.address)
        if (value.lon === '0' || value.lat === '0') {
          _this.$toast('定位数据获取失败！')
          self.btnState = true
        } else {
          _this.locationMsg.lng = value.lon
          _this.locationMsg.lat = value.lat
          _this.locationMsg.address1 = value.address
        }
      } else {
        console.info('固定坐标-----', self.gaodeDW.lng, self.gaodeDW.lat, '临时坐标-----', value.lon, value.lat)
        if (value.lon === '0' || value.lat === '0') {
          loading.hideLoading()
          self.$toast('定位数据获取失败！')
          self.btnState = true
        } else {
          if (self.GetDistance(self.userInfo.dzwlbj, self.gaodeDW.lng, self.gaodeDW.lat, value.lon, value.lat)) {
            loading.hideLoading()
            self.goKqtxl()
          } else {
            loading.hideLoading()
            self.$toast('距离过远，请调整当前位置')
            self.btnState = true
          }
        }
      }
    }
    // window['isAddressPermission'] = function(value) { // 校验是否开启获取位置权限
    //   console.log('校验结果-------', value)
    //   if (value === '0') { // 已开启
    //     self.getLocation()
    //     self.getAddressType = 'jjbg'
    //     self.showJjbg = true // 展示居家办公输入框
    //   } else {
    //     self.$toast('请先开启获取位置权限！')
    //   }
    // }
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 2)
    const _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    _this.date = _this.getNowTime()
    this.timer = setInterval(() => {
      _this.date = _this.getNowTime() // 修改数据date
    }, 1000)

    this.QdAmData = this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1 ? constants.QdAmData_dzwl : constants.QdAmData_zc // 上午正常员工数据
    console.info('dzwlqx------', this.userInfo.dzwlqx)
    console.info('QdAmData------', this.QdAmData)
    this.QdShareDataList = constants.QdShareDataList // 全部数据类型
    // this.QdAmData_dzwl = constants.QdAmData_dzwl // 电子围栏员工数据
    this.QdPmData = constants.QdPmData // 下午正常员工数据
    if (this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1) {
      this.showTip = true
    }
    if (this.userInfo.yhzb !== null && this.userInfo.yhzb !== undefined && this.userInfo.yhzb !== '') {
      if (this.userInfo.yhzb.lng !== '' && this.userInfo.yhzb.lat !== '') {
        var gaode = this.bd_decrypt(this.userInfo.yhzb.lng, this.userInfo.yhzb.lat)
        this.gaodeDW.lng = gaode.lng
        this.gaodeDW.lat = gaode.lat
      }
    }
    this.getKqData()
  },
  methods: {
    getLocation() {
      const pin = {
        'type': '1'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.getDistance(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
          const datas = JSON.parse(data)
          this.locationMsg.lng = datas.lng
          this.locationMsg.lat = datas.lat
          this.locationMsg.address1 = datas.address
          console.info('临时坐标-----', datas.lng, datas.lat, datas.address)
          console.info('getDistance------', this.locationMsg.address)
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    showImage(imagePath) {
      ImagePreview({
        images: [
          imagePath
        ],
        startPosition: 0,
        onClose() {
          // do something
        }
      })
    },
    getNowTime() {
      const date = new Date()
      let hours = ''
      let minute = ''
      let second = ''
      if (date.getHours() < 10) {
        hours = '0' + date.getHours()
      } else {
        hours = date.getHours()
      }
      if (date.getMinutes() < 10) {
        minute = '0' + date.getMinutes()
      } else {
        minute = date.getMinutes()
      }
      if (date.getSeconds() < 10) {
        second = '0' + date.getSeconds()
      } else {
        second = date.getSeconds()
      }
      return date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日' + '  ' + hours + ':' + minute + ':' + second
    },
    getKqData() {
      this.$store.dispatch('erp/querySignAction', {
        userId: this.userInfo.id
        // userId: '5a9c10a46b9689cb016bb11e3fac1556'
      }).then((res) => {
        console.info('res------', res)
        const signData = res.sign
        for (let index = 0; index < signData.length; index++) {
          const element = signData[index]
          this.handleData(element)
        }
        console.info('this.pmUpdateQdData------', this.pmUpdateQdData)
        console.info('this.amUpdateQdData------', this.amUpdateQdData)
        // this.$toast.success('登录成功！')
        // // this.$router.push({ name: 'MhHome' })
        // this.$router.push({ name: 'KqTj' })
      }).catch((error) => {
        this.$toast(error)
      })
    },
    // 展示弹出框
    showPopupKqlx(item) {
      if (item.worktype === 'qj') {
        this.$toast.fail('请假状态下不能修改考勤状态，请联系人事专员进行修改')
        return
      }
      if (item.worktype === 'qjspz') {
        this.$toast.fail('请假审批中，考勤状态不可修改')
        return
      }
      // 1 是 0否
      if (this.userInfo.dzwlqx === 1 || this.userInfo.dzwlqx === '1') { //  电子围栏
        if (item.dayType === '上午' && item.worktype !== 'wzt') {
          this.$toast.fail('您已开启电子围栏，不能修改状态，如确需修改请和人事专责联系并报备部门经理')
          return
        }
      }

      if (this.checkIndex !== '') {
        if (this.QdAmData[this.checkIndex].title_en === 'cc') {
          this.showCsdd = true
          this.showJjbg = false
          this.showSnbs = false
        } else {
          this.showCsdd = false
        }
        if (this.QdAmData[this.checkIndex].title_en === 'jjbg') {
          this.showJjbg = true
          this.showCsdd = false
          this.showSnbs = false
        } else {
          this.showJjbg = false
        }
        if (this.QdAmData[this.checkIndex].title_en === 'bs') {
          this.showJjbg = false
          this.showCsdd = false
          this.showSnbs = true
        } else {
          this.showSnbs = false
        }
      } else {
        this.showJjbg = false
        this.showCsdd = false
        this.showSnbs = false
      }

      // 0-1-上午 2-3-下午
      if (item.dayType === '上午') {
        this.QdAmData = this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1 ? constants.QdAmData_dzwl : constants.QdAmData_zc // 上午正常员工数据
        if (item.worktype === 'wc') {
          this.amUpdateQdData[0].times = 0.5
          // this.amUpdateQdData[1].worktype = ''
          this.amUpdateQdData.splice(1, 1)
          return
        }
        this.showPopup = 0
      } else if (item.dayType === '下午') {
        this.QdAmData = constants.QdPmData
        if (item.worktype === 'wc') {
          this.pmUpdateQdData[0].times = 0.5
          // this.pmUpdateQdData[1].worktype = ''
          this.pmUpdateQdData.splice(1, 1)
          return
        }
        this.showPopup = 1
      }

      // this.showPopup = type
      // if (item.worktype === 'wc') {
      //   this.updateQdData[1].worktype = '' // 清空上午因私外出状态
      // } else if (type === 3) {
      //   this.updateQdData[3].worktype = '' // 清空下午因私外出状态
      // }
      this.KqlxShow = true
    },
    // 提交居家办公信息
    qr(type) {
      if (type === 0) {
        this.locationMsg.address1 = ''
        this.getLocation()
      } else {
        if (this.locationMsg.address1) {
          if (this.showPopup === 0) {
            this.amUpdateQdData[0].address = this.locationMsg.address1
            this.amUpdateQdData[0].source = 1
            this.amUpdateQdData[0].worktype = 'jjbg'
          } else {
            this.pmUpdateQdData[0].worktype = 'jjbg'
            this.pmUpdateQdData[0].source = 1
            this.pmUpdateQdData[0].address = this.locationMsg.address1
          }
          this.KqlxShow = false
        } else {
          this.$toast('获取地址失败，请重新获取！')
        }
      }
    },
    addSnType(index) {
      // 清空场地信息
      if (index === 0) {
        this.bsdd = ''
        return
      }
      if (this.checkPopupList < 0) {
        this.$toast.fail('请选择考勤类型')
        return
      }
      if (this.bsdd.length === 0) {
        this.$toast.fail('请输入办事地点')
        return
      }
      if (this.showPopup === 0) {
        this.amUpdateQdData[0].address = this.bsdd
        this.amUpdateQdData[0].source = 1
        this.amUpdateQdData[0].worktype = 'bs'
      } else {
        this.pmUpdateQdData[0].worktype = 'bs'
        this.pmUpdateQdData[0].source = 1
        this.pmUpdateQdData[0].address = this.bsdd
      }
      this.KqlxShow = false
    },
    // 提交出差信息
    addCcType(index) {
      // 清空场地信息
      if (index === 0) {
        this.csdd = ''
        return
      }
      if (this.checkPopupList < 0) {
        this.$toast.fail('请选择考勤类型')
        return
      }
      if (this.csdd.length === 0) {
        this.$toast.fail('请输入出差地点，到市一级，例如：广东佛山市')
        return
      }
      if (this.showPopup === 0) {
        // this.amUpdateQdData[0].title = '出差---' + this.csdd
        this.amUpdateQdData[0].address = this.csdd
        this.amUpdateQdData[0].source = 1
        this.amUpdateQdData[0].worktype = 'cc'
      } else {
        // this.pmUpdateQdData[0].worktype = '出差---' + this.csdd
        this.pmUpdateQdData[0].worktype = 'cc'
        this.pmUpdateQdData[0].source = 1
        this.pmUpdateQdData[0].address = this.csdd
      }
      this.KqlxShow = false
    },
    handleData(element) {
      if (element.mark_noon === '上午') {
        if (this.amUpdateQdData[0].id === '') {
          this.amUpdateQdData[0].id = element.id
          this.amUpdateQdData[0].times = element.days_span
          this.amUpdateQdData[0].address = element.address
          this.amUpdateQdData[0].source = element.source
          this.amUpdateQdData[0].worktype = element.task_type
        } else {
          const wc = { id: element.id, dayType: '上午', times: element.days_span, worktype: element.task_type, source: element.source, address: element.address
          }
          this.amUpdateQdData.push(wc)
        }
        // this.amUpdateQdData.push(newObj)
      } else {
        if (this.pmUpdateQdData[0].id === '') {
          this.pmUpdateQdData[0].id = element.id
          this.pmUpdateQdData[0].times = element.days_span
          this.pmUpdateQdData[0].address = element.address
          this.pmUpdateQdData[0].source = element.source
          this.pmUpdateQdData[0].worktype = element.task_type
        } else {
          const wc = { id: element.id, dayType: '下午', times: element.days_span, worktype: element.task_type, source: element.source, address: element.address
          }
          this.pmUpdateQdData.push(wc)
        }
      }
    },
    // 选择签到状态
    cheackQdType(index) {
      this.getAddressType = ''
      this.checkIndex = index
      // console.info('showPopup==A==', this.showPopup)
      // 如果选择因私外出

      if (this.QdAmData[index].title_en === 'wc') {
        if (this.amUpdateQdData[0].id === '' || this.pmUpdateQdData[0].id === '') {
          this.$toast.fail('未获得考勤信息状态下不能选择因私外出')
          return
        }
        if (this.showPopup === 0) { // 上午
          if (this.amUpdateQdData[0].worktype !== 'qj' && this.amUpdateQdData[0].worktype !== 'tx') {
            // 展示下午因私外出
            // this.amUpdateQdData[1].worktype = 'wc'
            this.amUpdateQdData[0].times = 0.25
            const wc = { id: '', dayType: '上午', times: 0.25, worktype: 'wc', source: '1', address: ''
            }
            if (this.amUpdateQdData.length === 1) {
              this.amUpdateQdData.push(wc)
            }
          } else {
            this.$toast.fail('请假或调休状态下不能选择因私外出')
          }
        } else {
          if (this.pmUpdateQdData[0].worktype !== 'qj' && this.pmUpdateQdData[0].worktype !== 'tx') {
            // 展示下午因私外出
            this.pmUpdateQdData[0].times = 0.25
            const wc = { id: '', dayType: '下午', times: 0.25, worktype: 'wc', source: '1', address: ''
            }
            if (this.pmUpdateQdData.length === 1) {
              this.pmUpdateQdData.push(wc)
            }
          } else {
            this.$toast.fail('请假或调休状态下不能选择因私外出')
          }
        }
        this.KqlxShow = false
      } else if (this.QdAmData[index].title_en === 'cc') { // 选择出差
        // 如果上午有隐私外出 则删除
        if (this.amUpdateQdData.length > 1) {
          this.amUpdateQdData.splice(1.1)
        }
        // 如果下午有隐私外出 则删除
        if (this.pmUpdateQdData.length > 1) {
          this.pmUpdateQdData.splice(1.1)
        }
        this.checkPopupList = index
        this.showCsdd = true // 展示出差输入框
        this.showJjbg = false
        this.showSnbs = false
      } else if (this.QdAmData[index].title_en === 'jjbg') { // 选择居家办公
        console.log('this.locationMsg==================', this.locationMsg)
        this.getLocation()
        this.getAddressType = 'jjbg'
        this.showJjbg = true // 展示居家办公输入框
        this.showCsdd = false
        this.showSnbs = false
        this.checkPopupList = index
        // if (process.env.NODE_ENV !== 'development') {
        //   try {
        //     window.myapp.isAddressPermission('REQUEST_LOCATION')
        //   } catch (err) {
