        //   }
        //   this.$bridge.callhandler('isAddressPermission', '', (data) => {

        //   })
        //   this.checkPopupList = index
        // } else {
        //   this.$toast('返回原生页面')
        //   return
        // }
      } else if (this.QdAmData[index].title_en === 'bs') {
        this.checkPopupList = index
        this.showSnbs = true
        this.showCsdd = false // 展示出差输入框
        this.showJjbg = false
      } else { // 其他状态
        this.KqlxShow = false
        console.log('this.QdAmData[index]============', this.checkIndex, this.QdAmData)
        if (this.showPopup === 0) { // 上午
          this.amUpdateQdData[0].worktype = this.QdAmData[index].title_en
          this.amUpdateQdData[0].source = 1
          this.amUpdateQdData[0].address = ''
        } else {
          this.pmUpdateQdData[0].worktype = this.QdAmData[index].title_en
          this.pmUpdateQdData[0].source = 1
          this.pmUpdateQdData[0].address = ''
        }
      }
      console.info('amUpdateQdData==A==', this.amUpdateQdData)
      console.info('pmUpdateQdData==A==', this.pmUpdateQdData)
    },
    // 发送签到数据
    sendQdSj() {
      if (!this.btnState) {
        return
      }
      this.btnState = false
      if (this.amUpdateQdData[0].worktype === 'wzt') {
        this.amUpdateQdData[0].worktype = ''
      }
      if (this.pmUpdateQdData[0].worktype === 'wzt') {
        this.pmUpdateQdData[0].worktype = ''
      }
      // 1是 0否
      var str = this.userInfo.yhzb
      console.log('123==================1',this.amUpdateQdData, this.pmUpdateQdData);
      // 如果有因私外出，不判断位置，直接保存
      for (let i = 0; i < this.amUpdateQdData.length; i++) {
        if(this.amUpdateQdData[i].worktype == 'wc'){
          this.goKqtxl()
          return
        }
      }
      for (let i = 0; i < this.pmUpdateQdData.length; i++) {
        if(this.pmUpdateQdData[i].worktype == 'wc'){
          this.goKqtxl()
          return
        }
      }

      if (this.userInfo.dzwlqx === 1 || this.userInfo.dzwlqx === '1') { //  模拟数据
        if (this.amUpdateQdData[0].worktype === 'zc') {
          console.info('str-----', str)
          if (str === null || str === undefined || str === '') {
            this.$toast.fail('没有定位数据，请在个人中心上传个人位置')
            this.btnState = true
            return
          }
          const pin = { 'baseLat': this.userInfo.yhzb.lat,
            'baseLng': this.userInfo.yhzb.lng,
            'dzwlbj': this.userInfo.dzwlbj, // 模拟数据
            // 'dzwlbj': 300, // 真是数据
            'type': '0'
          }
          loading.showLoading()

          if (process.env.NODE_ENV !== 'development') {
            try {
              window.myapp.getDistance(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
              const datas = JSON.parse(data)
              // var gaode = this.bd_decrypt(datas.lng, datas.lat)
              // var lnglat = new AMap.LngLat(117.186602, 34.261579)// 测量点
              console.info('固定坐标-----', this.gaodeDW.lng, this.gaodeDW.lat, '临时坐标-----', datas.lng, datas.lat)
              if (this.GetDistance(this.userInfo.dzwlbj, this.gaodeDW.lng, this.gaodeDW.lat, datas.lng, datas.lat)) {
                loading.hideLoading()
                this.goKqtxl()
              } else {
                loading.hideLoading()
                this.$toast('距离过远，请调整当前位置')
                this.btnState = true
              }
              console.info('getDistance------', datas)
            })
          } else {
            this.btnState = true
            this.$toast('返回原生页面')
          }
        } else {
          this.goKqtxl()
        }
      } else {
        // const pin = { 'baseLat': '',
        //   'baseLng': '',
        //   'dzwlbj': '', // 模拟数据
        //   // 'dzwlbj': 300, // 真是数据
        //   'type': '2'
        // }
        // loading.showLoading()
        // if (process.env.NODE_ENV !== 'development') {
        //   try {
        //     window.myapp.getDistance(JSON.stringify(pin))
        //   } catch (err) {
        //     console.log(err)
        //   }
        //   this.$bridge.callhandler('getDistance', JSON.stringify(pin), (value) => {
        //     loading.hideLoading()
        //     if (value.lon === '0' || value.lat === '0') {
        //       loading.hideLoading()
        //       this.$toast('定位数据获取失败！')
        //       this.btnState = true
        //     } else {
        //       loading.hideLoading()
        //       if (this.amUpdateQdData[0].worktype === 'zc') {
        //         if (!this.amUpdateQdData[0].address) {
        //           this.amUpdateQdData[0].address = value.address
        //         }
        //       }
        //       if (this.pmUpdateQdData[0].worktype === 'zc') {
        //         if (!this.pmUpdateQdData[0].address) {
        //           this.pmUpdateQdData[0].address = value.address
        //         }
        //       }
        //       this.goKqtxl()
        //     }
        //   })
        // } else {
        //   this.btnState = true
        //   loading.hideLoading()
        //   this.$toast('返回原生页面')
        // }
        this.goKqtxl()
      }
      // console.info('updateQdData--11--', testArray)
    },
    // 方法定义 lat,lng
    GetDistance(dis, lat1, lng1, lat2, lng2) {
      var radLat1 = lat1 * Math.PI / 180.0
      var radLat2 = lat2 * Math.PI / 180.0
      var a = radLat1 - radLat2
      var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0
      var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
      Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)))
      s = s * 6378.137 // EARTH_RADIUS;
      s = Math.round(s * 10000)
      console.info('s-------------', s)
      console.info('dis-------------', dis)
      const dd = dis + 500
      console.info('dd-------------', dd)
      if (dd >= s) {
        return true
      } else {
        return false
      }
    },
    // 百度坐标转高德（传入经度、纬度）
    bd_decrypt(bd_lng, bd_lat) {
      var X_PI = Math.PI * 3000.0 / 180.0
      var x = bd_lng - 0.0065
      var y = bd_lat - 0.006
      var z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * X_PI)
      var theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * X_PI)
      var gg_lng = z * Math.cos(theta)
      var gg_lat = z * Math.sin(theta)
      return { lng: gg_lng, lat: gg_lat }
    },
    // 处理签到数据
    goKqtxl() {

      var arrys = this.amUpdateQdData.concat(this.pmUpdateQdData)
      console.log('123==================2',arrys);

      const arr = []
      const datas = { 'params': arrys,
        'username': getUserName(),
        'userId': this.userInfo.id
        // 'userId': '5a9c10a46b9689cb016bb11e3fac1556'
      }
      this.isdisabled = true
      arr.push(datas)

      const encryptor = new JSEncrypt()
      encryptor.setPublicKey(this.$store.state.mh.jmkey)
      const encryptedArr = []

      const baseStr = JSON.stringify(arr)
      console.log('加密前：', baseStr)

      if (baseStr.length > 10) {
        const num = baseStr.length / 10
        for (let i = 0; i <= num; i++) {
          console.log('分割加密前===========', baseStr.slice(i * 10, (i + 1) * 10))
          const jmstr = encryptor.encrypt(baseStr.slice(i * 10, (i + 1) * 10))
          encryptedArr.push(jmstr)
        }
      }
      console.log('加密后：', encryptedArr)
      this.$store.dispatch('erp/submitSignInfoAction', {
        userid: this.userInfo.id,
        data: encryptedArr,
        timestamp: new Date().getTime()
      }).then((res) => {
        this.isdisabled = false

        console.info('res------', res)
        this.amUpdateQdData = [{ id: '', dayType: '上午', times: 0.5, worktype: 'wzt', source: '1', address: ''
        }] // 上午签到数据数据类型
        this.pmUpdateQdData = [{ id: '', dayType: '下午', times: 0.5, worktype: 'wzt', source: '1', address: ''
        }]
        const signData = res.data.sign
        for (let index = 0; index < signData.length; index++) {
          const element = signData[index]
          this.handleData(element)
        }
        this.$toast(res.text)
        this.btnState = true
        // // this.$router.push({ name: 'MhHome' })
        // this.$router.push({ name: 'KqTj' })
      }).catch((error) => {
        this.btnState = true
        this.isdisabled = false
        this.$toast(error)
      })
    }
  }
}
